.group:hover:not(:focus-within) div.m-select-spending-transaction__control {
    --tw-border-opacity: 1 !important;
    border-color: rgb(113 113 122 / var(--tw-border-opacity)) !important;
}
    .group:hover .m-select-spending-transaction__value-container,
    .group:hover .m-select-spending-transaction__input-container,
    .group:hover .m-select-spending-transaction__single-value {
    font-size: 1rem;
    line-height: 1.5rem;
    font-weight: 500;
}
    :is(.dark .group:hover .m-select-spending-transaction__value-container),:is(.dark 
    .group:hover .m-select-spending-transaction__input-container),:is(.dark 
    .group:hover .m-select-spending-transaction__single-value) {
    --tw-text-opacity: 1;
    color: rgb(250 250 250 / var(--tw-text-opacity));
}
  .group div.m-select-spending-transaction__control {
    transition: unset;

    /* Because this is in the sidebar, it might have the blurred background */
    /* beneath the actual select component, make sure that part renders */
    /* correctly. But this way the dropdown itself still has a solid background */
    background-color: transparent;
  }
  /* Only show the border and indicator separator when the react-select */
  /* is hovered or focuses. */
  .group div.m-select-spending-transaction__control:not(:hover):not(:focus-within) {
    border-color: transparent;
}
  .group div.m-select-spending-transaction__control:not(:hover):not(:focus-within) .m-select-spending-transaction__indicators .m-select-spending-transaction__indicator-separator,
          .group div.m-select-spending-transaction__control:not(:hover):not(:focus-within) .m-select-spending-transaction__indicators .m-select-spending-transaction__dropdown-indicator {
    opacity: 0;
}
  /* Make the value text displayed a bit bigger because this is a significant */
  /* component in the sidebar */
  .group div.m-select-spending-transaction__control .m-select-spending-transaction__value-container,
    .group div.m-select-spending-transaction__control .m-select-spending-transaction__input-container,
    .group div.m-select-spending-transaction__control .m-select-spending-transaction__single-value {
    font-size: 1rem;
    line-height: 1.5rem;
    font-weight: 500;
}
  :is(.dark .group div.m-select-spending-transaction__control .m-select-spending-transaction__value-container),:is(.dark 
    .group div.m-select-spending-transaction__control .m-select-spending-transaction__input-container),:is(.dark 
    .group div.m-select-spending-transaction__control .m-select-spending-transaction__single-value) {
    --tw-text-opacity: 1;
    color: rgb(161 161 170 / var(--tw-text-opacity));
}

